
// rutas de vue JS

import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from './views/DashBoard.vue';
import Home from './views/Home.vue';


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Dashboard',
    name: 'Dashboard',
    component: Dashboard
  }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
